<template>
  <el-dialog title="" :visible.sync="visible" width="1065px">
    <div class="box">
      <video id="videoCamera" class="canvas" :width="videoWidth" :height="videoHeight" autoPlay></video>
      <canvas id="canvasCamera" class="canvas" :width="videoWidth" :height="videoHeight"></canvas>
    </div>
    <div slot="footer" style="display:flex">
		<el-upload
		  :action="$base.url + 'file/upload'"
		  :on-success="uploadSuccess"
		  :show-file-list="false"
		  accept=".jpg,.png,.jpge"
		  style="margin-right:10px">
		  <el-button icon="el-icon-upload2" size="small">
		    
		  </el-button>
		</el-upload>
      
      <el-button @click="drawImage" icon="el-icon-camera" size="small">
        
      </el-button>
      <el-button v-if="os" @click="getCompetence" icon="el-icon-video-camera" size="small">
        
      </el-button>
      <el-button v-else @click="stopNavigator" icon="el-icon-switch-button" size="small">
        
      </el-button>
      <el-button @click="resetCanvas" icon="el-icon-refresh" size="small">
        
      </el-button>
      <el-button @click="onCancel" icon="el-icon-circle-close" size="small">
        
      </el-button>
    </div>
  </el-dialog>
</template>
<script>
  export default {
    name: "XXX",
    data() {
      return {
		baseUrl:`/${this.$base.name}/`,
        visible: false, //
        loading: false, //
        os: false, //
        thisVideo: null,
        thisContext: null,
        thisCancas: null,
        videoWidth: 500,
        videoHeight: 400,
        postOptions: [],
        CertCtl: '',
        // 
        loading: true,
        // 
        ids: [],
        // 
        single: true,
        // 
        multiple: true,
        // 
        total: 0,
        // 
        akworkerList: [],
        //
        workerTypeOptions: [],
        // 
        title: "",
        // 
        open: false,
        // 
        queryParams: {
          pageNum: 1,
          pageSize: 10,
          imgSrc: undefined,
        },
        // 

        form: {},

        // 

        rules: {

        }

      };

    },

    created() {
      // this.onTake()
    },
	
    methods: {

      /**/

      onTake() {

        this.visible = true;

        this.getCompetence();

      },

      onCancel() {
		this.uploadImg()
        this.visible = false;
        /* this.resetCanvas();*/

        this.stopNavigator();

      },
	  uploadSuccess(e){
		  this.visible = false;
		  this.$emit('imgChange', e.file)
	  },
		uploadImg(){
			let param = new FormData()
			param.append('file',this.base64toFile(this.imgSrc))
			this.$http.post('file/upload',param).then(res=>{
				this.$emit('imgChange', res.data.file)
			})
		},
		base64toFile (dataurl, filename = 'file') {
		  let arr = dataurl.split(',')
		        let mime = arr[0].match(/:(.*?);/)[1]
		        let suffix = mime.split('/')[1]
		        let bstr = atob(arr[1])
		        let n = bstr.length
		        let u8arr = new Uint8Array(n)
		        while (n--) {
		          u8arr[n] = bstr.charCodeAt(n)
		        }
		        let file = new File([u8arr], `${filename}.${suffix}`, {
		          type: mime
		        })
		        return file
		},
      // 

      getCompetence() {

        //modelrenderdom,modeldom

        this.$nextTick(() => {

          const _this = this;

          this.os = false; //

          this.thisCancas = document.getElementById('canvasCamera');

          this.thisContext = this.thisCancas.getContext('2d');

          this.thisVideo = document.getElementById('videoCamera');

          // mediaDevices

          if (navigator.mediaDevices === undefined) {

            navigator.menavigatordiaDevices = {}

          }

          // mediaDevices

          // getUserMedia

          // getUserMedia

          if (navigator.mediaDevices.getUserMedia === undefined) {

            navigator.mediaDevices.getUserMedia = function(constraints) {

              // getUserMedia()

              let getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia || navigator
                .getUserMedia;

              // 

              // 

              if (!getUserMedia) {

                return Promise.reject(new Error('getUserMedia is not implemented in this browser'))

              }

              // Promisenavigator.getUserMedia

              return new Promise(function(resolve, reject) {

                getUserMedia.call(navigator, constraints, resolve, reject)

              })

            }

          }

          const constraints = {

            audio: false,

            video: {
              width: _this.videoWidth,
              height: _this.videoHeight,
              transform: 'scaleX(-1)'
            }

          };

          navigator.mediaDevices.getUserMedia(constraints).then(function(stream) {

            // srcObject

            if ('srcObject' in _this.thisVideo) {

              _this.thisVideo.srcObject = stream

            } else {

              // 

              _this.thisVideo.src = window.URL.createObjectURL(stream)

            }

            _this.thisVideo.onloadedmetadata = function(e) {

              _this.thisVideo.play()

            }

          }).catch(err => {

            this.$notify({

              title: '',

              message: '.',

              type: 'warning'

            });

          });

        });

      },

      //

      drawImage() {

        // canvas

        this.thisContext.drawImage(this.thisVideo, 0, 0, this.videoWidth, this.videoHeight);

        // base64

        this.imgSrc = this.thisCancas.toDataURL('image/png');

        /*const imgSrc=this.imgSrc;*/

      },

      //

      clearCanvas(id) {

        let c = document.getElementById(id);

        let cxt = c.getContext("2d");

        cxt.clearRect(0, 0, c.width, c.height);

      },

      //

      resetCanvas() {

        this.imgSrc = "";

        this.clearCanvas('canvasCamera');

      },

      //

      stopNavigator() {

        if (this.thisVideo && this.thisVideo !== null) {

          this.thisVideo.srcObject.getTracks()[0].stop();

          this.os = true; //

        }

      },

      /**/

    }

  };
</script>
<style rel="stylesheet/scss" lang="scss" scoped>

